}

.navbar{
	list-style-type: none;
	margin: 0;
	padding: 0;
	overflow: hidden;
    width: 100%;
	background-color: #A33;
	padding-left: 20px;
	border-top:1px solid #f1f1f1;
	padding-left: 20px;
	li {
		float: left;
		margin: 1px 10px 0 0;
	}
    li a{
    	display: block;
    	padding: 8px 16px;
    }
