.container {
	/*top: 100px; */
	
	position: absolute; 
	top:136px;
	/*top:36px;*/

	width: 100%; 
	height: auto; 
	bottom: 0px; 
	overflow: auto; 
	min-height: 250px; 
	min-width: 550px; 
	background-color: rgb(241, 241, 241);
}
.textareacontainer {
	/*
	width: 100%; 
	height: 50%; 
	*/
	width: 50%; 
	height: 100%; 

	float: left;
	/* background-color: rgb(255, 100, 100); */
}
.iframecontainer {
	/*
	width: 100%; 
	height: 50%; 
	*/

	width: 50%; 
	height: 100%; 
	
	float: left;
	/* background-color: rgb(100, 100, 255); */
}
.textarea {
	padding: 5px 5px; 
	width: 100%; 
	height: 100%;
}
.iframe {
	padding: 5px 5px; 
	width: 100%; 
	height: 100%;
}
.headerText {
	width: auto; 
	line-height: 2; 
	font-family: verdana; 
	font-size: 1em; 
	float: left;
}
.headerText2 {
	width: auto; 
	line-height: 2; 
	font-family: verdana; 
	font-size: 1em; 
	float: right;
}
.seeResult {
	padding: 0px 15px; 
	border: 1px solid #383F96; 
	border-image: none; 
	width: auto; 
	height: 35px;
	color: rgb(255, 255, 255); 
	line-height: 1.45; 
	font-size: 15px; 
	float: right; 
	cursor: pointer; 
	box-shadow: 0px 1px 3px rgba(0,0,0,0.12), 0px 1px 2px rgba(0,0,0,0.24); 
	background-color: #383F96;
	margin-left:4px;
}
.seeResult:hover {
	color: rgb(0, 0, 0); background-color: rgb(255, 255, 255);
}
.seeResult:active {
	background-color: rgb(241, 241, 241);
}
.textareawrapper {
	width: 100%; 
	height: 98%; 
	box-shadow: 0px 1px 3px rgba(0,0,0,0.12), 0px 1px 2px rgba(0,0,0,0.24); 
	background-color: rgb(255, 255, 255);
}
.iframewrapper {
	width: 100%; 
	height: 98%; 
	box-shadow: 0px 1px 3px rgba(0,0,0,0.12), 0px 1px 2px rgba(0,0,0,0.24); 
	background-color: rgb(255, 255, 255); -webkit-overflow-scrolling: touch;
}
#textareaCode {
	padding: 8px; border: currentColor; border-image: none; width: 100%; height: 100%; font-family: consolas,"courier new",monospace; font-size: 15px; background-color: rgb(255, 255, 255); resize: none;
}
#iframeResult {
	width: 100%; height: 100%; background-color: rgb(255, 255, 255);
}
#iframeSource {
	width: 100%; height: 100%; background-color: rgb(255, 255, 255);
}
.footerText {
	width: 100%; height: 20px; text-align: right; bottom: 0px; overflow: hidden; padding-right: 15px; font-family: verdana; font-size: 0.8em; position: absolute;
}
.footerText a {
	color: rgb(0, 0, 0);
}
.footerText a:hover {
	text-decoration: none;
}
#tryitLeaderboard {
	text-align: center; overflow: hidden; margin-top: 0px;
}
@media screen and (max-width:768px) {
	.footerText {
		display: none;
	}
}

@media screen and (max-width:728px) {
	#tryitLeaderboard {
		margin-top: 0px;
	}
	.container {
		top: 66px;
	}
	.trytopnav {
		top: 30px;
	}
	// .logo {
	// 	max-width:90px;
	// 	height:auto;
	// }
	.logo-small {
		display: block;
	}
	#navtxt{ 
		display:none;
	}
	.navbar button span {
		display:none;
	}
	.navbar form {
		display:none;
	}
}

@media screen and (max-width:467px) {
	.container {
		top: 56px;
	}
	.trytopnav {
		top: 20px;
	}
	.logo-small {
		display: block;
	}
	#navtxt{
		display:none;
	}
}

@media only screen and (max-device-width:768px) {
	#textareaCode {
		padding: 5px;
	}
	.iframewrapper {
		overflow: auto;
	}
	.container {
		min-width: 320px;
	}
	.textarea {
		height: 97%;
	}
	.iframe {
		height: 97%;
	}
}

@media screen and (max-height:700px) {
	.footerText {
		display: none;
	}
}

.loader {
  border: 16px solid #f3f3f3;
  border-radius: 50%;
  border-top: 16px solid grey;
  border-bottom: 16px solid grey;
  width: 60px;
  height: 60px;
  -webkit-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
}


@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
